import React, { useState } from 'react';
import { Box, TextField, Button, Typography } from '@mui/material';

const SavingsGoalCalculator = () => {
  const [goal, setGoal] = useState('');
  const [monthlySavings, setMonthlySavings] = useState('');
  const [interestRate, setInterestRate] = useState('');
  const [time, setTime] = useState(null);

  const calculateTime = () => {
    const r = interestRate / 100 / 12;
    const n = Math.log(1 + (goal * r) / monthlySavings) / Math.log(1 + r);
    setTime(n);
  };

  const clearFields = () => {
    setGoal('');
    setMonthlySavings('');
    setInterestRate('');
    setTime(null);
  };

  return (
    <Box className="container">
      <Typography variant="h4" gutterBottom>
        Savings Goal Calculator
      </Typography>
      <TextField
        label="Savings Goal Amount"
        variant="outlined"
        fullWidth
        value={goal}
        onChange={(e) => setGoal(e.target.value)}
        type="number"
        sx={{ marginBottom: 2 }}
      />
      <TextField
        label="Monthly Savings Amount"
        variant="outlined"
        fullWidth
        value={monthlySavings}
        onChange={(e) => setMonthlySavings(e.target.value)}
        type="number"
        sx={{ marginBottom: 2 }}
      />
      <TextField
        label="Annual Interest Rate (%)"
        variant="outlined"
        fullWidth
        value={interestRate}
        onChange={(e) => setInterestRate(e.target.value)}
        type="number"
        sx={{ marginBottom: 2 }}
      />
      <Button variant="contained" color="primary" onClick={calculateTime} sx={{ marginRight: 2, marginBottom: 2 }}>
        Calculate Time
      </Button>
      <Button variant="contained" color="secondary" onClick={clearFields} sx={{ marginBottom: 2 }}>
        Clear
      </Button>
      {time !== null && (
        <Typography variant="h6" gutterBottom>
          Months to Reach Goal: {time.toFixed(2)}
        </Typography>
      )}
    </Box>
  );
};

export default SavingsGoalCalculator;
